import { request } from 'utils';

const prefix = '/api/finance';

export const getBillsManage = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled, isEnrolled: true, ConfigTypeId: 0 },
    data: {},
  });

export const getBills = ({ page, pageSize = 10, orderBy, isPagingEnabled = true } = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'GET',
    params: { page, pageSize, orderBy, isPagingEnabled, isEnrolled: false, ConfigTypeId: 1 },
    data: {},
  });

export const searchBill = ({
  fiscalYear,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/GetReceipConfigByFiscalYear`,
    method: 'GET',
    params: {
      fiscalYear,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const getBillById = ({ id } = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/${id}`,
    method: 'GET',
    params: {},
    data: {},
  });

export const createBill = ({
  label,
  fiscalYear,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  noOfDelete,
  noOfLost,
  deletedReceipt,
  lostReceipt,
  isActive,
  isEnrolled,
} = {}) =>
  request({
    prefix,
    url: '/ReceiptConfig',
    method: 'POST',
    params: {},
    data: {
      label,
      fiscalYear,
      invoiceTemplate,
      invoiceCode,
      previousRemainFrom,
      previousRemainTo,
      publishFrom,
      publishTo,
      noOfDelete,
      noOfLost,
      deletedReceipt,
      lostReceipt,
      isActive,
      isEnrolled,
    },
  });

export const updateBill = ({
  id,
  label,
  invoiceTemplate,
  invoiceCode,
  previousRemainFrom,
  previousRemainTo,
  publishFrom,
  publishTo,
  noOfDelete,
  noOfLost,
  deletedReceipt,
  lostReceipt,
} = {}) =>
  request({
    prefix,
    url: `/ReceiptConfig/${id}`,
    method: 'PUT',
    params: {},
    data: {
      id,
      label,
      invoiceTemplate,
      invoiceCode,
      previousRemainFrom,
      previousRemainTo,
      publishFrom,
      publishTo,
      noOfDelete,
      noOfLost,
      deletedReceipt,
      lostReceipt,
    },
  });

export const deleteBill = id =>
  request({
    prefix,
    url: `/ReceiptConfig/${id}`,
    method: 'DELETE',
    params: {},
    data: {},
  });



